<template>
    <div class="horse-list-wrapper" ref="listRef">
        <div v-if="showSwitch" class="operate-wrap">
            <div class="list-type-wrapper">
                <div
                    class="list-type-item"
                    :class="listType === item.value && 'active'"
                    :key="item.value"
                    v-for="item in listTypes"
                    @click="listType = item.value"
                >
                    {{ item.label }}
                </div>
            </div>
        </div>
        <!-- 搜索 -->
        <PvxSearch :items="searchProps" :initValue="initValue" @search="searchEvent($event)"></PvxSearch>
        <!-- 预留抓马播报 -->
        <!-- 列表 -->
        <div class="horse-list-content">
            <div class="horse-list" :class="[listType + '-wrap']" v-loading="loading">
                <div v-if="listType === 'card'" class="list-content">
                    <HorseCard :item="item" v-for="item in list" :key="item.ID"></HorseCard>
                    <el-button
                        class="more-btn"
                        :disabled="!hasNextPage"
                        @click="loadData"
                        :loading="loading"
                        icon="el-icon-arrow-down"
                        >{{ hasNextPage ? "加载更多" : "没有更多了" }}</el-button
                    >
                </div>
                <div v-if="listType === 'list'" class="list-content">
                    <ListHead></ListHead>
                    <HorseItem :item="item" v-for="item in list" :key="item.ID"></HorseItem>
                    <pagination
                        v-show="totalPages > 0"
                        :total="total"
                        :page.sync="query.page"
                        :limit.sync="query.pageSize"
                        @pagination="findList(false)"
                    />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { __imgPath } from "@jx3box/jx3box-common/data/jx3box.json";
import { getHorses, getFeeds, getAttrs } from "@/service/horse";
import HorseCard from "@/components/horse/HorseCard";
import ListHead from "@/components/horse/ListHead";
import HorseItem from "@/components/horse/HorseItem";
import PvxSearch from "@/components/PvxSearch.vue";
import { deleteNull, isPhone } from "@/utils/index";
import { iconLink } from "@jx3box/jx3box-common/js/utils";
export default {
    name: "HorseList",
    props: [],
    components: {
        ListHead,
        HorseItem,
        HorseCard,
        PvxSearch,
    },
    data() {
        return {
            loading: false,
            listType: "card",
            listTypes: [
                {
                    value: "list",
                    label: "列表",
                },
                {
                    value: "card",
                    label: "卡片",
                },
            ],
            showSwitch: true,
            feeds: [],
            list: [],
            query: {
                page: 1,
                pageSize: 20,
            },
            total: 0,
            totalPages: 0,
            search: {},
            initValue: {},
            searchProps: [
                {
                    key: "type",
                    name: "类型",
                    type: "radio",
                    options: [
                        {
                            type: "",
                            name: "全部",
                        },
                        {
                            type: 0,
                            name: "普通坐骑",
                        },
                        {
                            type: 1,
                            name: "奇趣坐骑",
                        },
                        {
                            type: 2,
                            name: "马具",
                        },
                    ],
                },
                {
                    type: "filter",
                    key: "filter",
                    name: "过滤",
                    options: [
                        {
                            key: "feed",
                            type: "checkbox",
                            name: "喂食饲料",
                            options: [],
                        },
                        {
                            key: "attr",
                            type: "checkbox",
                            name: "属性",
                            options: [],
                        },
                        // {
                        //     key: "feed",
                        //     type: "select",
                        //     name: "喂食饲料",
                        //     multiple: true,
                        //     options: [],
                        // },
                        // {
                        //     key: "attr",
                        //     type: "select",
                        //     name: "属性",
                        //     filterable: true,
                        //     remote: "getAttrList",
                        // },
                    ],
                },
                {
                    key: "keyword",
                    name: "名称/ID",
                },
            ],
        };
    },
    computed: {
        hasNextPage: function () {
            return this.totalPages > 1 && this.query.page < this.totalPages;
        },
        client: function () {
            return this.$store.state.client;
        },
    },
    watch: {
        // isLine(bol) {
        //     // 变为一行时，如果不是第一页，重新请求第一页
        //     if (bol && this.query.page !== 1) {
        //         this.query.page = 1;
        //         this.query.pageSize = 20;
        //         this.findList();
        //     }
        // },
        listType: {
            handler(type) {
                this.query.page = 1;
                if (type === "card") {
                    this.showCount();
                } else {
                    this.query.pageSize = 20;
                    this.findList();
                }
            },
        },
        search: {
            deep: true,
            handler(search) {
                // 重置
                if (!Object.keys(search).length) {
                    return this.$router.push({ path: "/" });
                }
                // 点击全部
                if (search.type === "") {
                    search = {};
                    return this.$router.push({ path: "/" });
                }
                this.query.page = 1;
                if (this.listType === "card") {
                    this.showCount();
                } else {
                    this.query.pageSize = 20;
                    this.findList();
                }
            },
        },
    },
    methods: {
        async getAttrList(params = {}) {
            await getAttrs(params).then((res) => {
                // const data = res.data.concat([{ name: "双骑" }, { name: "渡水" }]);
                // const options = data
                //     .filter((item) => item.name.indexOf(params.name) > -1)
                //     .map((item) => {
                //         return {
                //             label: item.name,
                //             value: item.name,
                //         };
                //     });
                const data = res.data;
                const options = data.map((item) => {
                    return {
                        label: item.name,
                        value: item.name,
                    };
                });
                this.searchProps[1].options[1].options = options;
            });
        },
        iconLink,
        getFeed(item) {
            let feed = "";
            if (item.SubType === 15) {
                feed = this.feeds.find((fitem) => fitem.id === item.DetailType)
                    ? this.feeds.find((fitem) => fitem.id === item.DetailType).feed
                    : "";
            }
            return feed;
        },
        // 可否双骑
        canDouble(item) {
            let name = "";
            if (item.SubType === 15) {
                if (item.MagicAttributes && item.MagicAttributes.length) {
                    name = item.MagicAttributes.find((attr) => attr.id === "15650")
                        ? item.MagicAttributes.find((attr) => attr.id === "15650").name
                        : "单骑";
                }
            }
            return name;
        },
        getType(item) {
            // SubType 15为坐骑 23 为马具
            // DetailType 0普通坐骑，非0奇趣坐骑, 非0决定feed
            // DetailType 0头饰，1鞍饰，2足饰，3马饰
            let type = "";
            if (item.SubType === 15) {
                if (item.DetailType === 0) {
                    type = "普通坐骑";
                } else {
                    type = "奇趣坐骑";
                }
            } else if (item.SubType === 23) {
                if (item.DetailType === 0) {
                    type = "头饰";
                } else if (item.DetailType === 1) {
                    type = "鞍饰";
                } else if (item.DetailType === 2) {
                    type = "足饰";
                } else if (item.DetailType === 3) {
                    type = "马饰";
                } else {
                    type = "马具";
                }
            }
            return type;
        },
        isPhone,
        searchEvent(data) {
            this.search = data;
        },
        loadData() {
            this.query.page++;
            this.findList(true);
        },
        findList: function (isAppend = false) {
            this.loading = true;
            const search = Object.assign({}, this.search);
            for (let key in search) {
                if (Array.isArray(search[key])) {
                    search[key] = search[key].toString();
                }
            }
            const query = Object.assign({}, this.query);
            const params = Object.assign(search, query);
            params.client = this.client;
            getHorses(deleteNull(params))
                .then((res) => {
                    let newList = res.data.list.map((item) => {
                        item.typeName = this.getType(item);
                        item.modeName = this.canDouble(item);
                        item.feedName = this.getFeed(item);
                        if (item.MoveSpeed) {
                            item.speed = item.MoveSpeedDesc.split("移动速度提高")[1];
                        }
                        if (item.MagicAttributes && item.MagicAttributes.length) {
                            item.MagicAttributes.map((mItem) => {
                                mItem.iconUrl = this.iconLink(mItem.icon);
                                return mItem;
                            });
                        }
                        return item;
                    });
                    if (isAppend) {
                        this.list = this.list.concat(newList);
                    } else {
                        this.list = newList;
                    }
                    this.total = res.data.total;
                    this.totalPages = res.data.pages;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        async getFeedList() {
            await getFeeds({ client: this.client }).then((res) => {
                const arr = res.data.map((item) => {
                    const start = item.tip.indexOf("【");
                    const end = item.tip.indexOf("】");
                    item.feed = item.tip.slice(start + 1, end);
                    return item;
                });
                this.feeds = JSON.parse(JSON.stringify(arr));
                const newArr = [];
                arr.forEach((item) => {
                    const index = newArr.findIndex((nItem) => nItem.feed === item.feed);
                    if (index > -1) {
                        newArr[index].id += "," + item.id;
                    } else {
                        newArr.push(item);
                    }
                });

                const options = newArr.map((item) => {
                    return {
                        label: item.feed,
                        value: item.id,
                    };
                });
                this.searchProps[1].options[0].options = options;
            });
        },
        // 按宽度显示个数
        showCount(bol = true) {
            this.$nextTick(() => {
                const listWidth = this.$refs.listRef?.clientWidth;
                this.query.pageSize = Math.floor(listWidth / 200) * 4;
                bol && this.findList();
            });
        },
        jdugeType() {
            const self = this;
            setTimeout(() => {
                self.showSwitch = !self.isPhone();
                if (self.isPhone()) {
                    self.listType = "card";
                }
            }, 10);
        },
    },
    created() {
        const { params } = this.$route;
        if (params.type === "" || params.type === undefined) {
            params.type = null;
        }
        this.initValue = params;
    },
    mounted: function () {
        this.jdugeType();
        const self = this;
        window.onresize = function () {
            self.jdugeType();
        };
        this.getFeedList().then(() => {
            if (this.listType === "card") {
                this.showCount(false);
            }
        });
        this.getAttrList({ client: this.client });
        // window.onresize = () => {
        //     if (this.listType === "card") {
        //         // 卡片模式下大小改变重新计算数量并请求
        //         const oldSize = this.query.pageSize;
        //         const listWidth = this.$refs.listRef?.clientWidth;
        //         const newSize = Math.floor(listWidth / 220) * 4;
        //         if (oldSize !== newSize) {
        //             this.query.pageSize = newSize;
        //             this.findList();
        //         }
        //     }
        // };
    },
};
</script>

<style lang="less">
@import "~@/assets/css/horse/list.less";
</style>
